@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body{background-image:url(/react-star-wars/static/media/background4.0fd3fcc6.gif);background-repeat:no-repeat;background-size:100% 100%;height:100vh;font-family:'Press Start 2P', cursive}body Button{font-family:'Press Start 2P', cursive}.Container{display:flex;height:80vh}.Container .Content{display:flex;flex-direction:column;justify-content:center;width:100%;text-align:center;color:#fff}

.Home{display:flex;flex-direction:column;align-items:center;padding-top:25vh;color:#fff}.Home img{max-width:450px;width:100%}.Home h1{font-size:1.1rem;font-weight:bold;padding:20px}

.Btn-home{color:#000;font-size:1.7rem;border:none;text-decoration:none;cursor:pointer;padding:10px 50px;background-color:#ffc500;border-radius:15px;outline:none;box-shadow:1px 2px 5px 4px rgba(0,0,0,0.75)}

.Planets{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh}

.Planet-Card{max-width:420px;width:100%;padding:10px 10px 40px;background-color:#2b2745;border:3px solid #645e94;border-radius:10px;box-shadow:0px 5px 7px 3px #201c37}.Planet-Card h2{text-align:center;color:#ffc500;padding-bottom:10px}.Planet-Card h3{color:#fff;text-align:center;font-size:0.7rem;padding-bottom:5px}.Planet-Card-Content{background-color:#493f64;padding:10px 20px;border-radius:10px;font-size:1em;color:#fff}.Planet-Card-Content span{color:#ffc500}.Planet-footer{padding-top:30px;color:#fff;font-size:0.9rem;text-align:center}.Planet-footer span{color:#ffc500}.Btn-planet{margin-top:20px}.Btn-planet button{color:#000;font-size:1.5rem;border:none;text-decoration:none;cursor:pointer;padding:12px 50px;background-color:#ffc500;border-radius:15px;outline:none;box-shadow:1px 2px 5px 4px rgba(0,0,0,0.75)}

.Loading{display:flex;flex-direction:column;align-items:center;padding-top:25vh;color:#fff}.Loading p{color:#fff}.Loading img{max-width:230px;width:100%}

.Error{display:flex;justify-content:center;align-items:center;flex-direction:column;height:92vh}.Error h2,.Error p{color:#fff}.Btn-Error{color:#000;font-size:1.2rem;border:none;text-decoration:none;cursor:pointer;padding:10px 26px;background-color:#ffc500;border-radius:15px;outline:none;box-shadow:1px 2px 5px 4px rgba(0,0,0,0.75)}

