.Planet-Card {
    max-width: 420px;
    width: 100%;
    padding: 10px 10px 40px;
    background-color: #2b2745;
    border: 3px solid #645e94;
    border-radius: 10px;
    -webkit-box-shadow: 0px 5px 7px 3px rgba(32, 28, 55, 1);
    -moz-box-shadow: 0px 5px 7px 3px rgba(32, 28, 55, 1);
    box-shadow: 0px 5px 7px 3px rgba(32, 28, 55, 1);

    h2 {
        text-align: center;
        color: #ffc500;
        padding-bottom: 10px;
    }

    h3 {
        color: #fff;
        text-align: center;
        font-size: 0.7rem;
        padding-bottom: 5px;
    }
}
.Planet-Card-Content {
    background-color: #493f64;
    padding: 10px 20px;
    border-radius: 10px;
    font-size: 1em;
    color: #fff;
    span {
        color: #ffc500;
    }
}
.Planet-footer {
    padding-top: 30px;
    color: #fff;
    font-size: 0.9rem;
    text-align: center;
    span {
        color: #ffc500;
    }
}
.Btn-planet {
    margin-top: 20px;

    button {
        color: #000;
        font-size: 1.5rem;

        border: none;
        text-decoration: none;
        cursor: pointer;
        padding: 12px 50px;
        background-color: #ffc500;
        border-radius: 15px;
        outline: none;

        -webkit-box-shadow: 1px 2px 5px 4px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 1px 2px 5px 4px rgba(0, 0, 0, 0.75);
        box-shadow: 1px 2px 5px 4px rgba(0, 0, 0, 0.75);
    }
}
